import {Routes, RouterModule} from '@angular/router';
import {MainLayoutComponent} from "./shared/layout/app-layouts/main-layout.component";
import {AuthLayoutComponent} from "./shared/layout/app-layouts/auth-layout.component";
import {ModuleWithProviders} from "@angular/core";

import { CallbackComponent } from './callback/callback.component';
import { AuthGuard } from './services/auth-guard.service';


export const routes: Routes = [
        
    
    {
        path: '',      
        component: MainLayoutComponent,
       // canActivate: [AuthGuard],
        
        children: [
            {
                path: '', redirectTo: 'home', pathMatch: 'full'
            },
           
            {
                path: 'home',
                loadChildren: 'app/+home/home.module#HomeModule',
                data: {pageTitle: 'Home'}
            },
            {
                path: 'data',
                loadChildren: 'app/+data/data.module#DataModule',
                data: {pageTitle: 'Data'},
                canActivateChild: [AuthGuard],
              //  canActivate: [AuthGuard],
            },
            {
                path: 'calculation',
                loadChildren: 'app/+calculation/calculation.module#CalculationModule',
                data: {pageTitle: 'Calculation'},
                canActivateChild: [AuthGuard],
             //   canActivate: [AuthGuard]
            },
            /*
            
            {
                path: 'callback',
                loadChildren: 'app/callback/callback.module#CallbackModule',
                data: {pageTitle: 'Callback'}
            }, */
        ]
    },
    { path: 'callback', component: CallbackComponent }, 
    { path: '**', redirectTo: 'home', pathMatch: 'full' }, //jeli adna cieka nie dziaa (404 error) to przelij do home
    
    
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes, {useHash: false, enableTracing: true }); //  zmieniem useHash na false, enabletracing tylko do debugowania



